import React from "react";
import Chart from "react-apexcharts";

export default class PieChart extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      newS:props.stname,
      options: {
        //series: [40, 30, 15, 15],
        //labels: ["Apple", "Mango", "Lemon", "Banana"]
        series: [props.series],
        labels:["active","confirmed","recovered","deaths"]
      }
    };
  }
  /*componentDidMount(){
   this.setState({
      series:this.state.newS
   })
  }*/
  render() {
    return (
       <>
       {this.state.newS}
      <div id="pieChart">
        <Chart
          options={this.state.options}
          series={this.state.options.series[0]}
          type="pie"
          width="50%"
          height="200%"
        />
      </div>
      </>
    );
  }
}

