import React from 'react'
import { useState ,useEffect } from 'react'
import './style.css';
//import Chart from 'react-apexcharts'
import Displaydata from "./displaydata";

export default function Countrydata(props) {
   const [data, setdata] = useState([]);
   const [newarr, setnewarr] = useState([]);

   const getdata = async () =>{
     // const api_data=await fetch("https://data.covid19india.org/data.json");
     const api_data=await fetch("https://api.rootnet.in/covid19-in/stats/latest");
      const res=await api_data.json();
      //console.log(res.data.regional[0])
      setdata(res.data.regional);
   }
   useEffect(() => {
     getdata();
   }, []);

   return (
      <div className="card">
         {
            
             data.map((curele,ind)=>{
                 if(curele.loc.toUpperCase() ==props.stname.toUpperCase() ){
                   return (
                      <>
                      
                     <div className="stateHead">
                            {curele.loc}
                     </div>
                     <div className="stcontainer">
                        {<div className="activeCase item">
                           Active <br/> {curele.totalConfirmed-curele.discharged}
                        </div>}
                        <div className="confirmed item">
                           Confirmed <br/> {curele.totalConfirmed}
                        </div>
                        <div className="recoveredCase item">
                           Recovered <br/> {curele.discharged}
                        </div>
                        <div className="deathsCase item">
                           Deaths <br/> {curele.deaths}
                        </div>
                     </div>
                     </>    
                   )
                }   
             })
             
         }
       
      </div>
   )
}


