import React, { useEffect, useState } from "react";

function CovidData() {
const [country, setCountry] = useState("");
const [cases, setCases] = useState("");
const [recovered, setRecovered] = useState("");
const [deaths, setDeaths] = useState("");
const [todayCases, setTodayCases] = useState("");
const [deathCases, setDeathCases] = useState("");
const [recoveredCases, setRecoveredCases] = useState("");
const [userInput, setUserInput] = useState("");

useEffect(() => {
	fetch("https://disease.sh/v3/covid-19/countries")
	.then((res) => res.json())
	.then((data) => {
		setData(data);
	});
}, []);

const setData = ({
	country,
	cases,
	deaths,
	recovered,
	todayCases,
	todayDeaths,
	todayRecovered,
}) => {
	setCountry(country);
	setCases(cases);
	setRecovered(recovered);
	setDeaths(deaths);
	setTodayCases(todayCases);
	setDeathCases(todayDeaths);
	setRecoveredCases(todayRecovered);
};

const handleSearch = (e) => {
	setUserInput(e.target.value);
};
const handleSubmit = (props) => {
	props.preventDefault();
	fetch(`https://disease.sh/v3/covid-19/countries/${userInput}`)
	.then((res) => res.json())
	.then((data) => {
		setData(data);
	});
};

return (
	<div >
	<div>
		<form className="formedit" onSubmit={handleSubmit}>
         {/* input county name */}
         <input className="inputtext" onChange={handleSearch} placeholder="Enter Country Name" />
         <input type="submit"/>
		</form>
	</div>

	{/* Showing the details of the country */}
	<div >
		<div className="countryh">{country} </div>
      <div className="stcontainer">
         {<div className="activeCase item">
            Active <br/> {cases-recovered}
         </div>}
         <div className="confirmed item">
            Confirmed <br/> {cases}
         </div>
         <div className="recoveredCase item">
            Recovered <br/> {recovered}
         </div>
         <div className="deathsCase item">
            Deaths <br/> {deaths}
         </div>
         <div className="todayCases item">
            Today Cases <br/> {todayCases}
         </div>
         <div className="recoveredCases item">
            Today Recovered <br/> {recoveredCases}
         </div>
         <div className="deathCases item">
            Today Death <br/> {deathCases}
         </div>
      </div>  
	   </div>
	</div>
);
}

export default CovidData;

