import React, { Component } from 'react'
import "./mycomponent/style.css";
import Countrydata from "./mycomponent/StateData";
import Navigationbar from "./navigationbar";

export default class mainfile extends Component {
   constructor(props){
      super(props)
      this.state={
         svalue:""
      }
      //create ref
      this.textInput=React.createRef();

   }
  
   submitForm=e=>{
      e.preventDefault();
      //console.log(this.textInput.current.value);
      var title=this.textInput.current.value;
      if(title==""){
         alert("Please enter State name");
      }
      else{
         this.setState({
         svalue : title
      })
      }
      this.textInput.current.value="";
   }
   render() {
      return (
         <div>
            {/*<h1>Covid-19 Tracker</h1>*/}
            <form className="formedit" onSubmit={this.submitForm}>
                  <input className="inputtext" type="text" placeholder="Enter state name" ref={this.textInput} autoComplete="off"/>
                  <input type="submit"/>
            </form>
            {//display state data
               <Countrydata stname={this.state.svalue}/>
            }
         </div>
      )
   }
}
