import React from 'react'
import { useState ,useEffect } from 'react'
import './mycomponent/style.css';

export default function Countrydata(props) {
   const [data, setdata] = useState([]);

   const getdata = async () =>{
      const api_data=await fetch("https://api.rootnet.in/covid19-in/stats/latest");
      const res=await api_data.json();
     // console.log(res.data.summary);
      setdata(res.data.summary);
   }
   useEffect(() => {
     getdata();
   }, []);

   return (
      <div className="card">
         {
            
             [data].map((curele,ind)=>{
                return (
                    <>
                     <div className="stateHead">
                            India
                     </div>
                     <div className="smtcontainer">
                        <div className="activeCase item">
                           Active <br/> {curele.total-curele.discharged}
                        </div>
                        <div className="confirmed item">
                           Confirmed <br/> {curele.total}
                        </div>
                        <div className="recoveredCase item">
                           Recovered <br/> {curele.discharged}
                        </div>
                        <div className="deathsCase item">
                           Deaths <br/> {curele.deaths}
                        </div>
                     </div>
                    </>    
                ) 
             })
             
         }
      </div>
   )
}



